import { Component, OnInit } from '@angular/core';
import {Utilities , UserAuthorities} from '../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import { Router , ActivatedRoute} from '@angular/router';
import {AccessControlService , AlertService} from '../../../services';
import {Observable} from 'rxjs';

@Component({
  selector: 'app-user-authorities',
  templateUrl: './user-authorities.component.html',
  styleUrls: ['./user-authorities.component.scss']
})
export class UserAuthoritiesComponent implements OnInit {


  public loading: boolean;
  public total: number;
  public auths: [];
  public authorites: UserAuthorities[];
  id: number|string ;
  public submitting;

  constructor( private access: AccessControlService , private Alert: AlertService , private route: ActivatedRoute, ) { }

  ngOnInit() {

    this.id = this.route.snapshot.params.id;

  }



  public getUserAccess(state: ClrDatagridStateInterface){

    this.access.getpaginatedUserAuthority(this.id , Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((resp: any) => {




        this.authorites = resp.conent;
        this.total =  resp.totalElements;
        console.log(resp);
      } , (err) => {




        console.log(err);

      });


    this.access.getSystemAuthorites ().subscribe((res: any) => {

         this.auths = res;
      });





  }



  Submit(x){



    this.submitting = true;
    console.log('.... we are now submiitting');


    const newAuth = {
      authorityIds: [x.authorityIds] ,
      userId: this.id
    };



    this.access.postUserAuthority(newAuth).subscribe((resp) => {



      alert('Success');
    }, (err) => {


      alert('Error');
      console.log(err);
    });




  }



}
