import {Component, OnInit} from '@angular/core';
import {
    Groups,
    Agents,
    Utilities,
    Question,
    OptionsList,
    QuestionOption
} from '../../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import {Router, ActivatedRoute} from '@angular/router';
import {AgentsService, AlertService, AnswersService, ExcelService, GroupsService} from '../../../../services';



@Component({selector: 'app-details-answers', templateUrl: './details-answers.component.html', styleUrls: ['./details-answers.component.scss']})
export class DetailsAnswersComponent implements OnInit {
    public loading : boolean;
    public group : Groups;
    public dialog : Boolean;
    public total : number;
    public agents : [];
    public agent : Agents[];
    public submitting;
    public status : boolean;
    public questions : any = [];
    public question : Question[];
    public optionList : OptionsList;
    public answers : any = [];
    public answerLists : any = [];
    public questionOptions : QuestionOption;
    id : number;
    projectNumber:any


    constructor(private agentService : AgentsService, private mymans : AnswersService, private excelService : ExcelService, private route : ActivatedRoute, private alert : AlertService, private groupService : GroupsService, private router : Router) {}

    ngOnInit() {

        this.id = this.route.snapshot.params.id;

        console.log(this.id)


    }

    public getAnswers(state : ClrDatagridStateInterface) {

        this.loading = false;

        this
            .mymans
            .OneAnswer(this.id)
            .subscribe((resp : any) => {

                console.log(resp)
                this.agents = resp.questionAnswers
                this.total = resp.totalElements

                this.questions.push({'Project Name':resp?.project?.name, "Project Description":resp?.project?.description,  'Client-Name':resp?.project?.client?.name ,'projectNumber':resp?.project?.projectNumber, "agent":resp.createdBy})
                this.projectNumber = resp?.agent?.user?.uuid
                this.loading = false;

            })

        this
            .groupService
            .getPaginatedGroups()
            .subscribe((res : any) => {
                this.group = res;

            });


            this.exportIT()
    }




    exportIT(){

      this
      .mymans
      .OneAnswer(this.id)
      .subscribe((resp : any) => {

          console.log(resp.questionAnswers)
          this.agents = resp.questionAnswers


          this
              .agents
              .map((x : any) => {

                  if (x.stringAnswerValue === '') {

                      x.optionAnswerList
                          .map((z : any) => {

                              console.log(z.questionOption.text)

                              return this
                                  .questions
                                  .push({"question": x.question.questionText, "answer": z.questionOption.text})

                          })

                  } else {



                      return this
                          .questions
                          .push({"question": x.question.questionText, "answer": x.stringAnswerValue})

                  }

              })




      })

    }




    exportexcel(x) : void
    {





        this.excelService.exportAsExcelFile(this.questions, this.projectNumber);

    }

    viewDetailAnswer(x) {

        this
            .router
            .navigate([`admin/detail-answer/${x.id}`]);

    }



}
