import { Component, OnInit } from '@angular/core';
import {Groups, Agents , Utilities, Question,OptionsList, QuestionOption} from '../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import { Router,ActivatedRoute} from '@angular/router';
import {AgentsService , AlertService ,
  AnswersService,
GroupsService , ExcelService} from '../../../services';


@Component({
  selector: 'app-answers',
  templateUrl: './answers.component.html',
  styleUrls: ['./answers.component.css']
})
export class AnswersComponent implements OnInit {

  public loading: boolean;
  public group: Groups;
  public dialog: Boolean;
  public total: number;
  public agents: [];
  public agent: Agents[];
  public submitting;
  public status: boolean;
  public questions : any = [];
  public question : Question[];
  public optionList : OptionsList;
  public answers : any = [];
  public answerLists : any = [];
  public questionOptions : QuestionOption;
  id: number;
  projectNumber:any


  constructor( private agentService: AgentsService,private excelService : ExcelService,
    private mymans : AnswersService,
    private route: ActivatedRoute, private alert: AlertService , private groupService: GroupsService ,  private router: Router) { }

  ngOnInit() {

    this.id = this.route.snapshot.params.id;

  }



  public getAnswers(state: ClrDatagridStateInterface){

    this.loading = false;

     this.mymans.getPaginatedAnswers(this.id,Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((resp:any)=>{


           console.log(resp)
          this.agents = resp.content;
          this.total = resp.totalElements;
          this.loading = false;

      })



    this.groupService.getPaginatedGroups().subscribe((res: any) => {
      this.group =  res;

    });


  }










  viewDetailAnswer(x){


    this
    .router
    .navigate([`admin/detail-answer/${x.id}`]);

  }

viewmap(x){

  console.log(x)
    this.router.navigate([`admin/maps/${x.id}`]);

  }


  }


