import { Component, OnInit } from '@angular/core';
import {Groups, Agents ,Projects, Utilities, Question,QuestionAndAnswer} from '../../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import { Router,ActivatedRoute} from '@angular/router';
import {SuveysService ,AlertService ,GroupsService ,ExcelService, ProjectsService, AnswersService } from '../../../../services';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { MatDialog } from '@angular/material/dialog';
import { AssignAgentComponent } from 'src/app/ui/admin-ui/Survey/assign-agent/assign-agent.component';

@Component({
  selector: 'app-surveys',
  templateUrl: './surveys.component.html',
  styleUrls: ['./surveys.component.scss']
})
export class SurveysComponent implements OnInit {


  public loading: boolean;
  public group: Groups;
  public client: Projects;
  public dialog: Boolean;
  public total: number;
  public agents: [];
  public agent: Question[];
  public submitting;
  public status: boolean;
  id: number;
  answers:[];
  finalArr:any=[];
  tempQuestion:any=[];
  tempAnswer:any= [];
  public questionAndAnswer: QuestionAndAnswer
result = {};
  constructor( private excelService: ExcelService,private agentService: SuveysService,private ans: AnswersService,public matdialog: MatDialog, private alert: AlertService , private groupService: GroupsService, private projectService: ProjectsService ,  private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {

  }

  public getAgents(state: ClrDatagridStateInterface){

    this.loading = false;
    this.agentService.getPaginatedSurvey(Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((resp: any) => {
           console.log(resp)
      this.agents = resp.content;
      this.total = resp.totalElements;
      this.loading = false;
      console.log(this.agents)

    }, (err) => {
      console.log(err);
      this.loading = false;

    });

    this.groupService.getPaginatedGroups().subscribe((res: any) => {
      this.group =  res;

    });

    this.projectService.getPaginatedProjects().subscribe((resp: any)=>{
      this.client = resp.content

    })
  }

  editSurvey(x){
    this
    .router
    .navigate([`admin/editsurvey/${x.id}`]);
  }

  viewAnswers(x){

    this
    .router
    .navigate([`admin/survey-answers/${x}`]);

  }

  map(x){

    this
    .router
    .navigate([`admin/maps/${x}`]);

  }

  assignAgent(x){

    const dialogRef= this.matdialog.open(AssignAgentComponent,{width:"40%",data:{id:x.id,name:x.name ,remove:false}})

  }

  unassignAgent(x){

    const dialogRef= this.matdialog.open(AssignAgentComponent,{width:"40%",data:{id:x.id,name:x.name ,remove:true}})

  }

del(x){

this.agentService.deleteSurvey(x).subscribe((resp=>{
  this.alert.success('Project Deleted');

}),err=>{

  this.alert.error(err.error.message)
})

}

deactivate(x){

  this.agentService.DeactivateSurvey(x).subscribe((yes =>{

    this.alert.success('Project Deactivated')
  }) ,(err)=>{

    this.alert.error(err.error.message)
  })

}

exportall(x){
  this.ans.getAllAnswers(x).subscribe((result:any)=>{
    this.answers = result.questionAndAnswer
console.log(this.answers)


var a = [];
var b = [];

   this.answers.map((z:any)=>{


      a.push(z.question)
      b.push(z.answer)
});


console.log(a)
console.log(b)


let r ={}
for (var  i = 0; i < a.length; i++) {
  r[a[i]] = b[i];
}

console.log(r);

  },err=>{
    console.log(err)
  })
}

export(){
  this.excelService.exportAsExcelFile(this.result as any, '')
}


}
