import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import { EditUserComponent } from 'src/app/ui/admin-ui/User/edit-user/edit-user.component';
import {SingleUserComponent} from '../../controllers/admin/user/single-user/single-user.component'
import {SingleProjectComponent} from './project/single-project/single-project.component'
import {DashboardComponent} from './dashboard/dashboard.component'
import { GroupAuthoritiesComponent } from './group-authorities/group-authorities.component';
import {UsersComponent} from './user/users/users.component'
import {UserAuthoritiesComponent} from './user-authorities/user-authorities.component'
import {PasswordManagementComponent} from '../auth/password-management/password-management.component'
import { ProjectsComponent } from './project/projects/projects.component';
import {AgentsComponent} from './agent/agents/agents.component'
import {SurveyFormComponent}  from '../../ui/admin-ui/Survey/survey-form/survey-form.component'
import {SingleAgentComponent} from './agent/single-agent/single-agent.component'
import {AnswersComponent} from './answers/answers.component'
import {DocumentsComponent} from './documents/documents.component'
import {EditSurveyComponent}  from  '../../ui/admin-ui/Survey/edit-survey/edit-survey.component'
import {ClientAgentComponent}  from './client-agent/client-agent.component'
import {MapsComponent} from './maps/maps.component'
import {SurveysComponent}  from './Surveys/surveys/surveys.component'
import {DetailsAnswersComponent}  from './answers/details-answers/details-answers.component'
const routes : Routes = [

    {
        path: '',
        pathMatch: 'full',
        component: DashboardComponent
    }, {
        path: 'users',
        component: UsersComponent
    },
    {
      path:'agents',
      component: AgentsComponent
    },
    {

      path:'clientagents',
      component:ClientAgentComponent
    },
    {
      path:'surveys',
      component:SurveyFormComponent
    },
    {


      path:'editsurvey/:id',
      component:EditSurveyComponent
    },
    {

      path:'allsurveys' ,
      component:SurveysComponent
    },
    {
      path: 'projects',
      component: ProjectsComponent
    },
    {
      path: 'user/:id/view',
      component: SingleUserComponent
  },
  {
    path: 'project/:id/view',
    component: SingleProjectComponent
},
{
  path:'agent/:id/view',
  component: SingleAgentComponent
},


{
path:'detail-answer/:id',
component:DetailsAnswersComponent


},
  {
    path: 'user/:id/edit',
    component: EditUserComponent
},
{
  path: 'group-authorities',
  component: GroupAuthoritiesComponent
},
{
  path: 'user-authorities/:id',
  component: UserAuthoritiesComponent
},
{
  path: 'password-management',
  component: PasswordManagementComponent
},
{
  path: 'survey-answers/:id',
  component: AnswersComponent
},
{
  path: 'documents',
  component: DocumentsComponent
},
{ 
  path: 'maps/:id',
  component: MapsComponent
}


];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AdminRoute {}
