import { Component, OnInit } from '@angular/core';
import {GroupAuthorities} from '../../../models'
import {AlertService ,GroupsService ,AccessControlService} from '../../../services'

import * as moment from 'moment'
import {Utilities ,UserAuthorities} from '../../../models'
import {ClrDatagridStateInterface} from '@clr/angular';

@Component({
  selector: 'app-group-authorities',
  templateUrl: './group-authorities.component.html',
  styleUrls: ['./group-authorities.component.scss']
})
export class GroupAuthoritiesComponent implements OnInit {

  public group
  public dialog:Boolean
  public total :number ;
  public submitting

  constructor( private alert:AlertService ,private access:AccessControlService ) { }
  public authorities: []
  ngOnInit() {


  }


  Submit(x){
    console.log('subbing')

 
  }




  public getAccess(state:ClrDatagridStateInterface){

    this.access.getPaginatedAuthorites(Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((res:any) =>{

      this.authorities = res.content;
      this.total= res.totalElements
   })
  }


}
