import {Component, OnInit} from '@angular/core';
import {Groups, Users, Utilities} from '../../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import {Router} from '@angular/router';
import {UsersService, AlertService, GroupsService} from '../../../../services';
import * as moment from 'moment';

@Component({selector: 'app-users', templateUrl: './users.component.html', styleUrls: ['./users.component.scss']})
export class UsersComponent implements OnInit {
    public loading : boolean;
    public group : Groups;
    public dialog : Boolean;
    public total : number;
    public users : [];
    public user : Users[];
    public submitting;
    public status : boolean;

    constructor(private userService : UsersService, private alert : AlertService, private groupService : GroupsService, private router : Router) {}

    ngOnInit() {}

    public getUsers(state : ClrDatagridStateInterface) {

        this.loading = false;
        this
            .userService
            .getPaginatedUsers(Utilities.formatDatagridState(state, state.page
                ? state.page.from / state.page.size
                : 0))
            .subscribe((resp : any) => {
                this.users = resp.content;
                this.total = resp.totalElements;
                this.loading = false;

            }, (err) => {
                console.log(err);
                this.loading = false;

            });

        this
            .groupService
            .getPaginatedGroups()
            .subscribe((res : any) => {
                this.group = res;

            });

    }

    Submit(x) {
        console.log('subbing');

        this.submitting = true;

        const newUser = {
            dateOfBirth:moment(x.name.dateOfBirth).format('DD/MM/YYYY'),
            driverLicenseNumber: x.other.driverLicenseNumber,
            email: x.contact.email,
            firstName: x.name.firstName,
            gender: x.name.gender,
            initials: x.name.initials,
            lastName: x.name.lastName,
            nationalIdentificationNumber: x.other.nationalIdentificationNumber,
            nationality: x.other.nationality,
            passportNumber: x.other.passportNumber,
            phoneNumber: x.contact.phoneNumber,
            groupId: x.other.groupId,
            title: x.name.title,
            username: x.name.username,
            id: ''
        }as Users;
        this
            .userService
            .newUser(newUser)
            .subscribe((resp) => {
                this
                    .alert
                    .success('New User Added');
                this.getUsers({});
                this.submitting = false;

            }, (err) => {
                this
                    .alert
                    .error(err.error.message);
                this.submitting = false;

            });
    }

    editUser(x) {
        this.user = x;
        this.dialog = true;
    }

    SubmitEdit(x) {



        console.log(x.name.dateOfBirth)
        const newUser = {
            dateOfBirth: x.name.dateOfBirth || moment(x.name.dateOfBirth).format('DD/MM/YYYY') ,
            driverLicenseNumber: x.other.driverLicenseNumber,
            email: x.contact.email,
            firstName: x.name.firstName,
            gender: x.name.gender,
            initials: x.name.initials,
            lastName: x.name.lastName,
            nationalIdentificationNumber: x.other.nationalIdentificationNumber,
            nationality: x.other.nationality,
            passportNumber: x.other.passportNumber,
            phoneNumber: x.contact.phoneNumber,
            groupId: x.other.groupId,
            title: x.name.title,
            username: x.name.username,
            id: x.name.id
        }as Users;

        this
            .userService
            .updateUserAdmin(newUser, x.name.id)
            .subscribe((resp) => {
                this
                    .alert
                    .success('User Updated');
                this.getUsers({});

            }, (err) => {
                this
                    .alert
                    .error(err.error.message);
                this.dialog = false;
            });

    }

    viewauthority(x) {

        this
            .router
            .navigate([`admin/user-authorities/${x.id}`]);

    }

    activateUser(x) {

        this
            .userService
            .changeUserAdminActivationStatus(!x.enabled, `${x.id}`)
            .subscribe(res => {
                console.log(res);
                this.getUsers({});
            }, err => {
                console.log(err);
            });

    }

    viewUser(x) {

        this
            .router
            .navigate([`admin/user/${x.id}/view`]);


    }



    Useropned(x){


        this.dialog = true
    }
}
