import { Component, OnInit } from '@angular/core';
import {Groups, Projects , Utilities} from '../../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import { Router} from '@angular/router';
import {ProjectsService , AlertService ,
GroupsService} from '../../../../services';
import * as moment from 'moment';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss']
})
export class ProjectsComponent implements OnInit {

  public loading: boolean;
  public group: Groups;
  public dialog: Boolean;
  public total: number;
  public projects: [];
  public project: Projects;
  public submitting;
  public status: boolean;

  constructor( private projectService: ProjectsService, private alert: AlertService ,
  private groupService: GroupsService ,  private router: Router) { }

  ngOnInit() {



  }



  public getProjects(state: ClrDatagridStateInterface){

    this.loading = false;
    this.projectService.getPaginatedProjects(Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((resp: any) => {
      this.projects = resp.content;
      this.total = resp.totalElements;
      this.loading = false;
      console.log(this.projects)

    }, (err) => {
      console.log(err);
      this.loading = false;

    });

    this.groupService.getPaginatedGroups().subscribe((res: any) => {
      this.group =  res;

    });

  }



  activateUser(x) {

    this
        .projectService
        .changeUserAdminActivationStatus(!x.enabled, `${x.id}`)
        .subscribe(res => {
            console.log(res);
            this.getProjects({});
        }, err => {
            console.log(err);
        });

}

viewauthority(x){

  this
  .router
  .navigate([`admin/user-authorities/${x.id}`]);

}

  Submit(x){
    console.log('subbing');

    this.submitting = true;

    const  newProject =  {
      description: x.description,
      email: x.email,
      name: x.name,
      phoneNumber: x.phoneNumber,
      username: x.username
    } as Projects;
    this.projectService.newUser(newProject).subscribe((resp) => {
      this.alert.success('New Project Added');
      this.getProjects({});
      this.submitting = false;
      console.log(newProject)



    }, (err) => {
      this.alert.error(err.error.message);
      this.submitting = false;
      console.log(newProject)

    });
  }




  editUser(x){
     this.project = x;
     this.dialog = true;


  }



  SubmitEdit(x){

     console.log(this.project)

    const  newProject =  {
      description: x.description,
      email: x.email,
      name: x.name,
      phoneNumber: x.phoneNumber,
      username: x.username
    } as Projects;


    this.projectService.updateProject(newProject ,this.project.id).subscribe((resp) => {
      this.alert.success('Project Updated');
      this.getProjects({});
      this.dialog =  false;


    }, (err) => {
      this.alert.error(err.error.message);
      this.dialog =  false;
    });



  }

  viewUser(x){


    this
    .router
    .navigate([`admin/project/${x.id}/view`]);

  }

}
