import {Component, OnInit} from '@angular/core';
import {AnswersService} from '../../../services';
import {ActivatedRoute, Router} from '@angular/router';

@Component({selector: 'app-maps', templateUrl: './maps.component.html', styleUrls: ['./maps.component.scss']})
export class MapsComponent implements OnInit {

    id : number;
    answer = []
    load = false
    arrayCoodinates : any = [];
    lat = -18.820770;
    long = 29.825491;
  
    zoom=7;

    constructor(private route : ActivatedRoute, private router : Router, private answerSer : AnswersService) {}
     ngOnInit() {
        this.id = this.route.snapshot.params.id;

        this.getAnnswe(this.id)


        

    }



  


 getAnnswe(xy) {

        this
            .answerSer
            .getPaginatedAnswers(xy)
            .subscribe((data : any) => {

                this.answer = data.content

                console.log(this.answer)
                this.cleanArray()


            });
  
    }

    cleanArray() {

        this
            .answer
            .map((x) => {

              this.load = true
                console.log(x + "eer")
                return this
                    .arrayCoodinates
                    .push({lat: x.latitude, long: x.longitude })
            })

      

    }
}
