import { Component, OnInit } from '@angular/core';
import {Groups, Agents ,Projects, Utilities} from '../../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import { Router} from '@angular/router';
import {AgentsService , AlertService ,GroupsService , ProjectsService } from '../../../../services';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-agents',
  templateUrl: './agents.component.html',
  styleUrls: ['./agents.component.css']
})
export class AgentsComponent implements OnInit {

  public loading: boolean;
  public group: Groups;
  public client: Projects;
  public dialog: Boolean;
  public total: number;
  public agents: [];
  public agent: Agents[];
  public submitting;
  public status: boolean;



  constructor( private agentService: AgentsService, private alert: AlertService , private groupService: GroupsService, private projectService: ProjectsService ,  private router: Router) { }

  ngOnInit() {



  }



  public getAgents(state: ClrDatagridStateInterface){

    this.loading = false;
    this.agentService.getPaginatedAgents(Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((resp: any) => {

      this.agents = resp.content;
      this.total = resp.totalElements;
      this.loading = false;
      console.log(this.agents)

    }, (err) => {
      console.log(err);
      this.loading = false;

    });

    this.groupService.getPaginatedGroups().subscribe((res: any) => {
      this.group =  res;


    });

    this.projectService.getPaginatedProjects().subscribe((resp: any)=>{
      this.client = resp.content


    })
  }

  activateUser(x) {

    this
        .agentService
        .changeUserAdminActivationStatus(!x.enabled, `${x.id}`)
        .subscribe(res => {
            console.log(res);
            this.getAgents({});
        }, err => {
            console.log(err);
        });

}

  Submit(x){
    console.log('subbing');

    this.submitting = true;


const newAgent ={
   createUserCommand:{
      dateOfBirth: moment(x.name.dateOfBirth).format('DD/MM/YYYY'),
      driverLicenseNumber: x.other.driverLicenseNumber,
      email: x.contact.email,
      firstName: x.name.firstName,
      gender: x.name.gender,
      initials: x.name.initials,
      lastName: x.name.lastName,
      nationalIdentificationNumber: x.other.nationalIdentificationNumber,
      nationality: x.other.nationality,
      passportNumber: x.other.passportNumber,
      phoneNumber: x.contact.phoneNumber,
      title: x.name.title,
      username: x.name.username,
      id: 0
    }
  } as any


    this.agentService.newAgent(newAgent).subscribe((resp) => {
      this.alert.success('New Agent Added');
      this.getAgents({});
      this.submitting = false;

      console.log(resp)



    }, (err) => {
      console.log(newAgent)
      this.alert.error(err.error.message);
      this.submitting = false;

    });
  }




  editAgent(x){
     this.agent = x;
     this.dialog = true;
  }



  SubmitEdit(x){

    const  updateAgent =  {
      updateUserCommand : {
      dateOfBirth:  x.name.dateOfBirth || moment(x.name.dateOfBirth).format('DD/MM/YYYY') ,
      driverLicenseNumber: x.other.driverLicenseNumber,
      email: x.contact.email,
      firstName: x.name.firstName,
      gender: x.name.gender,
      initials: x.name.initials,
      lastName: x.name.lastName,
      nationalIdentificationNumber: x.other.nationalIdentificationNumber,
      nationality: x.other.nationality,
      passportNumber: x.other.passportNumber,
      phoneNumber: x.contact.phoneNumber,
      groupId: x.other.groupId,
      title: x.name.title,
      username: x.name.username,
      id: x.name.id
      }
    } as any


    this.agentService.updateAgent(updateAgent , x.name.id).subscribe((resp) => {

      this.alert.success('Agent Updated');
      this.getAgents({});

    }, (err) => {
      this.alert.error(err.error.message);
      this.dialog =  false;
      console.log(updateAgent)
    });

  }





  viewauthority(x){

    this
    .router
    .navigate([`admin/user-authorities/${x.id}`]);

  }




  viewAgent(x){


    this
    .router
    .navigate([`admin/agent/${x.id}/view`]);

  }

}
