import { Component, OnInit } from '@angular/core';
import {Projects,Documents , Utilities} from '../../../models';
import {ClrDatagridStateInterface} from '@clr/angular';
import { Router} from '@angular/router';
import {ProjectsService,DocumentsService,AlertService } from '../../../services';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import {FormGroup,FormBuilder} from '@angular/forms'

@Component({
  selector: 'app-documents',
  templateUrl: './documents.component.html',
  styleUrls: ['./documents.component.css']
})
export class DocumentsComponent implements OnInit {

  public loading: boolean;
  public dialog: Boolean;
  public total: number;
  public projects: [];
  public documents: [];
  public document: Documents[];
  public project: Projects[];
  public submitting;
  public status: boolean;
  templateForm: FormGroup;
  file;

  constructor( private projectService: ProjectsService,private documentService: DocumentsService, private alert: AlertService ,
      private router: Router,private formBuilder: FormBuilder) { }

  ngOnInit() {
this.templateForm= this.formBuilder.group({
  fileName:['']
})

this.getProjects({});

  }
  public getProjects(state: ClrDatagridStateInterface){

    this.loading = false;
    this.documentService.getPaginatedDocuments(Utilities.formatDatagridState(state, state.page
      ? state.page.from / state.page.size
      : 0)).subscribe((resp: any) => {
      this.documents = resp.content;
      this.total = resp.totalElements;
      this.loading = false;
      console.log(this.documents)

    }, (err) => {
      console.log(err);
      this.loading = false;

    });

    this.projectService.getAllClients().subscribe((res:any)=>{
      this.project = res.content;

    })



  }
  Submit(x){
    console.log('subbing');

    this.submitting = true;


const newDocument ={
      fileName: x.fileName,
      clientId: x.clientId,
      name: x.fileName
  } as any



  let formData = new FormData()
    formData.append('file',this.file,this.file.name)
    console.log(this.file)
    this.documentService.uploadFile(formData).subscribe((res)=>{
      console.log(res)
      const newDoc:any={clientId:this.templateForm.value.clientId,fileName:res.fileName,name:this.file.name}

      this.documentService.newDocument(newDoc).subscribe((res)=>{
        this.alert.success('New Document uploaded');
        this.getProjects({});
      this.submitting = false;

      console.log(res)

      },(err)=>{
        this.alert.error(err.error.message);
      })
    },err=>{
      this.alert.error(err.error.message);
    })


  }
  uploadFile(x){
    if(x.target.files.length>0){
      this.file = x.target.files[0]

    }
  }

  deleteDocument(x){
    console.log(x)
    this.documentService.deleteDocument(x).subscribe((res)=>{
      this.getProjects({});
      this.alert.success("Document deleted successfully");

    },(err)=>{
      this.alert.error(err.message)
    })
}

}
